@mixin header{
	font-family: $fuente;
	grid-area:head;
	display: flex;
	position: fixed;
	min-height: 120px;
	background: linear-gradient(#194f4b,#b785d2);
	border-bottom: 2px solid black;
	z-index: 100;
	align-items:center;
	justify-content: center;
	flex-direction: column;
	width: 100%;
}
@mixin botonera {
			.botonera{
			display: flex;
			align-items: center;
			align-content: center;
			.botonera_menu{	
				li{
					position: relative;
					display: inline-block;
					padding: 0px 10px;
					a{
					  font-weight: $fuente-bold;
					  text-decoration: none;
					  color: black;
					  font-size: 30px
					}
					.botonera_hover{
						display: none;
						position: relative;
						background:linear-gradient(#b785d2a8,#194f4b);
				}
			}
		}
	}

}